import React from "react";
import "./AboutUs.scss";

const AboutUs = () => {
  return (
    <div className="about">
      <h2>Jak to wszystko si zaczo?</h2>
      <div className="about-container">
        <div className="about-img-box" />
        <div className="about-content">
          <p className="blockquote">
            "Boimy si tego, czego nie znamy. Jeli co jest skryte pod zason
            ciemnoci i niepewnoci, jest tajemnic. Pozwolenie, by wiato
            przedaro si przez t ciemno sprawia, e wszystko staje si
            jasne."
          </p>
          <p className="text-content">
            <span>Hodowla Valhalla Geckos</span> oficjalnie powstaa w grudniu
            2020 roku. Od tego czasu systematycznie si rozrasta, a ilo
            podopiecznych i Nasza wiedza na temat zwierzt terrarystycznych
            wci ronie.
            <br />
            <br />
            Pierwsze stycznoci ze zwierztami terrarystycznymi miaem ju w
            podstawwce, gdzie na specjalnie zorganizowanym dla dzieci pokazie
            po raz pierwszy w yciu trzymaem na szyi dorosego Boa dusiciela.
            By moe to wanie od tego zdarzenia gdzie gboko w sercu
            zakiekowao ziarno z ktrego wyrosa pasja i mio do zwierzt
            terrarystycznych. Popularny w tamtych czasach{" "}
            <a
              rel="noreferrer"
              target="_blank"
              href="https://pl.wikipedia.org/wiki/Steve_Irwin"
            >
              Steve Irwin - owca Krokodyli
            </a>{" "}
            - bdcy hitem na kanale Animal Planet, ktrego uwielbiaem oglda
            tylko t pasj wzmocni.
            <br />
            <br />
            Profesjonalnie terrarystyka zagocia w moim yciu w czasach szkoy
            redniej i studiw. W szczytowym okresie posiadaem okoo 50
            gatunkw ptasznikw. Z czasem miejsce ptasznikw zajy gady, a na
            chwil obecn w Naszym domu opiekujemy si gekonami orzsionymi oraz
            gekonami paczcymi. Nie jest to jednak nasze ostatnie sowo!
          </p>
        </div>
      </div>
    </div>
  );
};

export default AboutUs;
